.container {
  width: 80vw;
  max-width: 900px;
  display: block; 
  margin: auto;
}

form {
  margin-top: -60px !important;
  display: grid; 
  grid-template-columns: repeat(2, 1fr); 
  grid-template-rows: repeat(3, 1fr); 
  grid-row-gap: 50px;
  border-radius: 40px;
  padding: 40px;
  background-color: hsl(347, 62%, 95%);
  height: 600px !important;
}

.formWord input {
  grid-area: 1 / 1 / 2 / 2;
  width: 90%;
  height: 50px;
  margin-bottom: 45px;
  border: 0;
  margin-top: 7px;
  padding-left: 10px;
  background: #ffffff;
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
}

.formWord2 textarea {
  grid-area: 1 / 2 / 2 / 3;
  width: 100%;
  border: 0;
  margin-top: 5px;
  height: 300px;
  color: black;
  box-shadow: 0px 2px 4px 0px rgb(218, 218, 218);
  padding-top: 3%;
  padding-left: 3%;
}

.form-btn button {
  grid-area: 2 / 1 / 3 / 3; 
  width: 200%;
  height: 50px;
  margin-top: -50px !important;
  vertical-align: middle;
  margin-left: auto !important;
  margin-right: auto !important;
  border: 0;
  border-radius: 15px;
  background-color: #7ce095;
  transition: 0.3s linear;
  color: black;
  font-weight: bold;
}

form button:hover {
  background-color: snow;
  border: 2px solid #fca17d;
  transition: 0.3s linear;
  color: black;
  cursor: pointer;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px); 
  transition: 0.3s ease-in-out;
}

.submittedForm {
  grid-area: 3 / 1 / 4 / 3;
  padding-top: 1px;
  color: #001517;


}

.sentMsg {
  text-align: center;
  color: #2a292c
}

.lottie-bg{
  position: absolute;
  margin-top: -40px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
}

