@-webkit-keyframes cambio1080 {
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    from {margin-left: 0%}
    to {margin-left: -58.66%}

}

@keyframes cambio1080 {
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    from {margin-left: 0%}
    to {margin-left: -58.66%}

}

/* On Mobile */
@-webkit-keyframes cambioIphoneX {
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    from {margin-left: 0%}
    to {margin-left: -285.66%}
}
@keyframes cambioIphoneX {
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    from {margin-left: 0%}
    to {margin-left: -285.66%}
}


@media screen and (max-width: 480px) {
    .slider {
        -webkit-animation: cambioIphoneX 20s linear infinite;
                animation: cambioIphoneX 20s linear infinite;
    }
}

/* On 720px */
@-webkit-keyframes cambioMedium {
    100% {
        -webkit-transform: translateX(-100%);
