        -webkit-animation: cambioMedium 20s linear infinite;
                animation: cambioMedium 20s linear infinite;
    
    }
}

/* On Tablet */
@-webkit-keyframes cambioIpad {
    100% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
    from {margin-left: 0%}
